.about__container {
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, .25), 0 8px 15px -8px rgba(0, 0, 0, .3), 0 -6px 16px -6px rgba(0, 0, 0, .025);
    padding: 20px;
    border-radius: 5px;
    background: #fff;
    margin: 0 auto;
    margin-top: 100px;
    width: 100%;
    max-width: 400px;
    font-weight: 300;
}

.about__container span:first-child {
    display:block;
    font-size: 20px;
}